import {DynamicLocalize} from "InternalShared/Localization/DynamicLocalize";
import * as enUS  from 'date-fns/locale/en-US'

export class LocalizeDate {
    private static Locale:any;
    public static GetLocale(weekStartOn:number=0){
        if(LocalizeDate.Locale==null) {
            LocalizeDate.Locale = DynamicLocalize;
            (enUS as any).localize=LocalizeDate.Locale;
        }

        if(weekStartOn!=0)
        {
            let cloned=Object.assign({},enUS);
            (cloned as any).options.weekStartsOn=weekStartOn as any;
        }
        return enUS;
    }
}